.rightbar{
	flex: 3;
	margin-left: 5px;
	margin-top: 35px;
	height: 100vh;
}


.rightbarWrapper {
  padding: 20px 20px 0 0;
  margin-left: 20px;
}

.birthdayContainer {
  display: flex;
  align-items: center;
}

.birthdayImg {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.birthdayText {
  font-weight: 300;
  font-size: 15px;
}

.rightbarAd {
  width: 100%;
  height: 400px;
  border-radius: 10px;
  margin: 30px 0;
}

.rightbarTitle{
    margin-bottom: 20px;
}

.rightbarFriendList {
  padding: 0;
  margin: 0;
  list-style: none;
}

.rightbarTitle{
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
}

.rightbarInfo{
  margin-bottom: 30px;
}

.rightbarInfoItem{
  margin-bottom: 10px;
}

.rightbarInfoKey{
  font-weight: 500;
  margin-right: 15px;
  color: #555;
}

.rightbarInfoValue{
  font-weight: 300;
}

.rightbarFollowings{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.rightbarFollowing{
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  cursor: pointer;
}
.rightbarUsername{
	vertical-align: middle;
	position: absolute;
	margin-top: 9px;
	margin-left: 5px;
	align-items: center;
}

.rightbarFollowingImg{
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 5px;
}

.rightbarProfileImg{
	width: 40px;
	height: 40px;
	object-fit: cover;
	border-radius: 50%;
	display: inline;
	list-style: none;
}
.rightbarOnline{
	width:  123px;
	height: 15px;
	border-radius: 50%;
	background-color: green;
	position: relative;
	top: -25px;
	border: 1px solid white;
	right: 7px;
	color: green;

}